
.Hotbg
{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2f3640;
    height: 40px;
    border-radius: 40px;
    padding: 10px;
}
.fullength 
{
    width: 240px !important;
    padding: 0 6px !important;

}

.up
{
    top: 5% !important;
    width: 500px !important;

}
.Hotbg:hover > .Hotbg-txt
{
    width: 240px ;
    padding: 0 6px;

}

.Hotbg:hover > .Hotbg-btn
{
    background: azure;
    color: black;
}

.Hotbg-btn
{
    color: #e84118;
    float: right;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #2f3640;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    color: azure;
    cursor: pointer;
}

.Hotbg-btn > i
{
    font-size-adjust: 30px;
}

a
{
    text-decoration: none;
}

.Hotbg > .Hotbg-txt
{
    border: none;
    background: none;
    outline: none;
    float: left;
    padding: 0;
    color: azure;
    font-size: 16px;
    transition: 0.4s;
    line-height: 40px;
    width: 0px;
    font-weight: bold;
}
.tile {
    height: 15em;
    position: absolute;
    top: 16%;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
    background-color: rgb(24, 83, 56);
    
    border:1px solid #33353c;
    padding: 20px;
    border-radius: 30px;
    overflow: auto;
    margin: 10px;
}

.tile-title {
    color:rgb(94, 81, 24);
    font-size: 20px;
    font-weight: bold;
    font-family:Arial;
}

@media (min-width:1024px){
    .tile {
        width: 48%;
        float: left; 
    }
}
.tile:hover {
    transition: all .2s ease-in;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .tile-img{
    background-size: cover;
    background-position: center center;
    background-repeat: none;
    width: inherit;
    height: inherit;
  }
  
  .tile-info {
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    padding: 1em;
  }
.rightBoard{
    position: absolute;
    top: 20%;
    left: 70%;
    box-shadow: 0 24px 45px -15px;
    border:1px solid #33353c;
    padding: 20px;
    border-radius: 30px;

}